:root {
  --mainRed: #ff6667;
  --mainWhite: rgb(211, 211, 211);
  --mainBlack: black;
  --mainGrey: #302f2f;
  --mainTransition: all 0.3s linear;
  --mainSpacing: 3px;
  --lightShadow: 2px 5px 3px 0px rgba(0, 0, 0, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: rgb(211, 211, 211);
  color: var(--mainWhite);
}

.body {
  background: url("https://firebasestorage.googleapis.com/v0/b/andproducciones-b1b01.appspot.com/o/fondo.jpg?alt=media&token=bc60cda5-d1a0-4eb7-b90b-25880ad31234");
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  line-height: 1.4;
}

/* N A V B A R */
.navbar {
  top: 0;
  left: 0;
  padding: 0.75rem 2rem;
  width: 100%;
  background: black;
  background: var(--mainBlack);
  display: flex;
}

#logo {
  width: 100px;
  height: 40px;
}

.nav-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

.nav-icon {
  font-size: 1.5rem;
  color: rgb(211, 211, 211);
  color: var(--mainWhite);
}

.nav-links {
  height: 0;
  overflow: hidden;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
  -webkit-transition: var(--mainTransition);
  transition: var(--mainTransition);
  list-style-type: none;
}

.nav-menu {
  width: 600px;
}
.nav-links a {
  display: block;
  text-decoration: none;
  color: rgb(211, 211, 211);
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
  -webkit-transition: var(--mainTransition);
  transition: var(--mainTransition);
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 3px;
  letter-spacing: var(--mainSpacing);
}

.nav-links a:hover,
.nav-links a:active {
  color: #ff6667;
  color: var(--mainRed);
}

.show-nav {
  margin-top: 35px;
  height: 130px;
}

.show-nav li {
  margin-top: 10px;
}

@media screen and (min-width: 783px) {
  .nav-btn {
    display: none;
  }

  .nav-menu {
    max-width: 1170px;
    display: flex;
  }

  .nav-links {
    height: auto;
    display: flex;
  }

  .nav-links a {
    padding-top: 18px;
    margin: 2px 2rem;
  }
}

@media screen and (max-width: 782px) {
  nav.navbar {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .nav-logo {
    position: absolute;
    z-index: 1;
    width: 100px;
    height: 40px;
    left: 0;
    top: 25px;
    right: 0;
    margin: auto;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    -webkit-transition: var(--mainTransition);
    transition: var(--mainTransition);
  }

  .logo-center {
    top: 25px;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    -webkit-transition: var(--mainTransition);
    transition: var(--mainTransition);
  }

  .nav-btn {
    float: left;
  }

  .nav-menu {
    width: 100%;
    display: block;
    float: left;
    margin-left: auto;
    margin-right: auto;
  }

  .nav-links {
    float: bottom;
  }
}

/*  F I N   N A V B A R */

/* H O M E */

.eventoDefault {
  min-height: calc(100vh - 66px);
  background: url("http://djmagla.com/wp-content/uploads/2019/03/Florido-664x400.jpg")
    center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}

.eventoDefault h1 {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 700;
  color: rgb(255, 255, 255);
  letter-spacing: 8px;
  padding: 15px;
  background-color: black;
  background-color: var(--mainBlack);
  font-size: 80px;
  box-shadow: 0px 12px 0px -1px rgba(255, 102, 103, 0.81);
  margin-top: 450px;
}

.title {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 700;
  color: white;
  letter-spacing: 8px;
  font-size: 40px;
  text-align: center;
  margin-top: 50px;
}

.EventoDiv {
  display: flex;
  justify-content: center;
}

.EventoDiv a {
  margin-right: 20px;
  margin-top: 25px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
  font-size: 30px;
  box-shadow: 0 3px 3px 0.9px rgba(0, 0, 0, 0.2);
  /* ANIMACIÓN */
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(50%, black),
    color-stop(50%, #7f3334)
  );
  background-image: linear-gradient(
    to bottom,
    black 50%,
    #7f3334 50%
  );
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(50%, var(--mainBlack)),
    color-stop(50%, #7f3334)
  );
  background-image: linear-gradient(
    to bottom,
    var(--mainBlack) 50%,
    #7f3334 50%
  );
  -webkit-transition: background-position 1s;
  transition: background-position 1s;
  background-size: 100% 200%;
  color: rgb(211, 211, 211);
  color: var(--mainWhite);
}

.EventoDiv a:hover,
.EventoDiv a:active {
  cursor: pointer;
  background-position: 0 -100%;
}

@media screen and (max-width: 782px) {
  header.eventoPage {
    min-height: calc(78vh - 1px);
    background-position-x: 34%;
  }

  header.contactoPage {
    min-height: calc(78vh - 1px);
    background-position-x: 80%;
  }

  header.eventoDefault {
    min-height: calc(78vh - 1px);
  }

  .eventoDefault h1 {
    font-size: 8vw;
    box-shadow: 0px 12px 0px -1px rgba(255, 102, 103, 0.81);
  }

  .title {
    font-size: 6vw;
  }
}

.div-card-home {
  max-width: 100%;
  max-height: 1000px;
  width: 100%;
  margin: 20px auto;
  padding: 0;
  display: flex;
  justify-content: center;
}

/* CARTAS */
.div-card-events {
  padding-bottom: 52px;
  display: table;
  margin: 0 auto;
  width: 84%;
}

.card {
  float: left;
  margin: auto;
  width: 250px;
  height: 350px;
  margin-top: 25px;
  text-align: center;
  margin-right: 30px;
  box-shadow: 0 3px 3px 0.9px rgba(0, 0, 0, 0.2);
  /* ANIMACIÓN */
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(2%, #ff6667),
    color-stop(98%, black)
  );
  background-image: linear-gradient(
    to bottom,
    #ff6667 2%,
    black 98%
  );
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    color-stop(2%, var(--mainRed)),
    color-stop(98%, var(--mainBlack))
  );
  background-image: linear-gradient(
    to bottom,
    var(--mainRed) 2%,
    var(--mainBlack) 98%
  );
  -webkit-transition: background-position 1s;
  transition: background-position 1s;
  background-size: 100% 200%;
}

.card h2 {
  margin-top: 10px;
  font-size: 10px;
}

.card img {
  min-height: calc(10vh - 66px);
  filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
  -webkit-transition: -webkit-filter 0.4s;
  transition: -webkit-filter 0.4s;
  transition: filter 0.4s;
  transition: filter 0.4s, -webkit-filter 0.4s;
  -webkit-transition: -webkit-filter 1s;
}

.card:hover,
.card:active {
  cursor: pointer;
  background-position: 0 -100%;
}

.card:hover img,
.card:active img {
  filter: grayscale(0);
  -webkit-filter: grayscale(0);
}

@media screen and (max-width: 840px) {
  .card {
    display: block;
    margin-right: 15px;
    height: 362px;
  }

  .card img {
    width: 250px;
    margin: 0;
  }

  .div-card-home,
  .div-card-events {
    display: table;
    width: 25%;
  }
}

/*  FIN CARTAS */

/* SoundCloud */
